.header{
    width:100%;
    background:#fff;
    padding:0 25px;
    font-size:16px;
    line-height: 60px;
    display: flex;
    >a{
        flex:1;
        color:#393939;
        font-weight: 600;
        // border:1px solid red;
        cursor: pointer;
        span:nth-of-type(1){
            margin-right:10px; 
        }
    }
    .exit{
        &:hover{
            color:#e8e078;
        }
    }
    .selected{
        // color:red;
    }
    .cartNum{
      display: inline-block;
      width:30px;
      height: 30px;
      background: red;
      line-height: 30px;
      border-radius: 50%;
    }
    .active{
        border:1px solid red;
        
    }
}