.box{  
    display: flex;
    flex-wrap: wrap;
    justify-content:space-between;
    .item:nth-of-type(1){
        width: 100%;
        // height:400px;
        img{
            width:100%;
            height:100%;
        }
    }
}
.item{
    // flex:1;
    width:40%;

    // height: 300px;
    // border:1px solid red;
    margin:30px 0;
    position: relative;
    .cover{
        position: absolute;
        top:0;
        left:0;
        width:100%;
        // border:1px solid red;
        height:100%;
        background: #999;
        opacity: 0.4;
        &:hover{
            opacity: 0;
        }
        p{
            position: absolute;
            bottom: 20px;
            right:20px;
            font-size: 40px;
            font-weight: 800;
            color:#000;
            opacity: 1;
            
        }
    }
    // ::before{
    //     content:'11111';
    //     width:100%;
    //     height:100%;
    //     border:1px solid red;
    // }
    img{
        width: 100%;
    }
}